:root {
  --color-dark: #343a40;
  --color-dark-secondary: #161c2d;
  --color-grey: #8492a6;
  --color-grey-secondary: #f0f0fe;
  --color-white: #ffffff;
  --color-white-secondary: #f6f7fa;

  --color-blue: #007bff;
  --color-red: #e54b4b;
  --color-green: #01a479;
  --color-yellow: #fdd76e;
  --color-orange: #f95700;
  --color-teal: #21d4b9;
  --color-brown: #8b5e34;
  --color-pink: #fe6d73;
  --color-beige: #ecce96;
  --color-purple: #6c63ff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.no-container {
  margin-left: 0 !important;
  margin-right: 0 !important;
}

@font-face {
  font-family: "Nunito Bold";
  src: local("Nunito Bold"),
    url("./assets/fonts/Nunito/Nunito-Bold.ttf") format("woff");
}

@font-face {
  font-family: "Nunito Semi Bold";
  src: local("Nunito Semi Bold"),
    url("./assets/fonts/Nunito/Nunito-SemiBold.ttf") format("woff");
}

@font-face {
  font-family: "Nunito Regular";
  src: local("Nunito Regular"),
    url("./assets/fonts/Nunito/Nunito-Regular.ttf") format("woff");
}

@font-face {
  font-family: "Nunito Light";
  src: local("Nunito Light"),
    url("./assets/fonts/Nunito/Nunito-Light.ttf") format("woff");
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* SCROLL BAR CUSTOMIZATION  */
::-webkit-scrollbar {
  background: #eeeeee;
  border-radius: 4px;
  height: 8px;
  width: 8px;
}

::-webkit-scrollbar-thumb {
  background: var(--color-grey);
  border-radius: 4px;
}

/* ALIGNMENT STYLES */
.align-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.align-text-center {
  text-align: center;
}

/* TITLE & SUBTITLE RELATED STYLES */
.title-holder {
  padding: 50px 0px;
}

.title {
  font-size: 30px;
  letter-spacing: -0.5px;
  line-height: 1.2;
  display: block;
  font-family: "Nunito Semi Bold";
  font-weight: 600;
  color: var(--color-dark);
}

.sub-title {
  font-size: 15px;
  letter-spacing: -0.5px;
  line-height: 26px;
  font-family: "Nunito Regular";
  font-weight: 400;
  letter-spacing: 0.025px;
  color: var(--color-grey);
}
